import { useTranslation } from "react-i18next";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useState } from "react";
import { useIqacDashboardMaster } from "../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import style from "../../FinanceManager/FinanceManager.module.css";
import IqacMembersCard from "./IqacMembersCard";
import AddIqacMembers from "./AddIqacMembers";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
const IqacMembers = ({ qcid }) => {
  const { t } = useTranslation();
  const [add, setAdd] = useState(false);

  const {
    iqacDashboardMaster,
    iqacDashboardMasterLoading,
    iqacDashboardMasterRefetch,
  } = useIqacDashboardMaster({
    data: {
      qcid: qcid,
    },
    skip: !qcid,
  });

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("iqac_members")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    // onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {iqacDashboardMaster?.composition?.map((member) => (
            <IqacMembersCard member={member} key={member?._id} />
          ))}
          {iqacDashboardMasterLoading ? (
            <Loading />
          ) : (
            iqacDashboardMaster?.composition?.length === 0 && (
              <EmptyMessage title="No members." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <AddIqacMembers
          onClose={onToggle}
          qcid={qcid}
          onRefetch={iqacDashboardMasterRefetch}
          instituteId={iqacDashboardMaster?.institute}
        />
      )}
    </>
  );
};

export default IqacMembers;
