import AboutIqac from "./AboutIqac/AboutIqac";
import IqacAboutTabs from "./IqacAboutTabs";
import { useSearchParams } from "react-router-dom";
import IqacCommitteePhoto from "./IqacCommitteePhoto/IqacCommitteePhoto";
import IqacInstitutional from "./IqacInstitutional/IqacInstitutional";

const IqacAbout = () => {
  const [searchParams] = useSearchParams();

  return (
    <>
      {searchParams.get("n") === "iqac" ? (
        <AboutIqac />
      ) : searchParams.get("n") === "institutional" ? (
        <IqacInstitutional />
      ) : searchParams.get("n") === "iqac-committee" ? (
        <IqacCommitteePhoto />
      ) : (
        <IqacAboutTabs />
      )}
    </>
  );
};

export default IqacAbout;
