import { useLocation, useParams } from "react-router-dom";
import SquareTabWrapper from "../../../Ui/Tab/Square/SquareTabWrapper";
import SquareTabCard from "../../../Ui/Tab/Square/SquareTabCard";
import { assestsStoreUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
const IqacAboutTabs = () => {
  const params = useParams();
  const getQuery = useLocation();

  let measure = document.getElementById("7892b5ty")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 870 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 870 ? 1 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }

  return (
    <SquareTabWrapper dynamicId="7892b5ty">
      <SquareTabCard
        label="institutional_perspective_plan"
        iconUrl={`${assestsStoreUrl}/unit.svg`}
        url={`/q/${params.username}/member/staff/committee?type=about&n=institutional`}
        state={getQuery.state}
        isAuthorized
        // isVisible={tabManage ?? {}}
        isVisible={{
          store_unit: true,
        }}
        authorizedKey="store_unit"
        customStyleContainer={{
          height: height,
          width: width,
        }}
      />

      <SquareTabCard
        label="about_iqac"
        iconUrl={`${assestsStoreUrl}/individual.svg`}
        url={`/q/${params.username}/member/staff/committee?type=about&n=iqac`}
        state={getQuery.state}
        isAuthorized
        // isVisible={tabManage ?? {}}
        isVisible={{
          individual_staff: true,
        }}
        authorizedKey="individual_staff"
        customStyleContainer={{
          height: height,
          width: width,
        }}
      />

      <SquareTabCard
        label="iqac_committee_photo"
        iconUrl={`${assestsStoreUrl}/stock.svg`}
        url={`/q/${params.username}/member/staff/committee?type=about&n=iqac-committee`}
        state={getQuery.state}
        isAuthorized
        // isVisible={tabManage ?? {}}
        isVisible={{
          stock: true,
        }}
        authorizedKey="stock"
        customStyleContainer={{
          height: height,
          width: width,
        }}
      />
    </SquareTabWrapper>
  );
};

export default IqacAboutTabs;
